<script>
  import axios from "axios";

  export default {
    name: 'MainSlider',
    created() {
      this.fetchSlides()
    },

    data () {
      return {
        slideNum : 2,
        loading : false,
        colors: [
          'indigo',
          'warning',
          'pink darken-2',
          'red lighten-1',
          'deep-purple accent-4',
        ],
        slides : [{

        },{

        },{

        }],

        /*slides: [
          {
            desc : 'خانه ی هنر های',
            title : '<b>روایتگر</b>',
            address : 'https://arch.taakwin.ir/images/5-portfolio.jpg'
          },
          {
            desc : 'غذا های دریایی',
            title : '<b>در رستوران</b>',
            address : 'https://arch.taakwin.ir/images/12-portfolio.jpg'
          },
          {
            desc : 'آینده ای دوباره',
            title : '<b>در کلینیک</b>',
            address : 'https://arch.taakwin.ir/images/10-portfolio.jpg'
          }
        ],*/
      }
    },
    methods : {
      loaded(){
        this.$store.state.loading = false;
      },
      fetchSlides () {
        this.loading = true;
        axios.get(this.$store.state.host + '/website/api/v1/website/sliders').then(response=>{
          this.slides = response.data.data;
          this.loading = false;

        })
      },
    },
    computed : {
      mobileDisplay () {
        switch (this.$vuetify.breakpoint.name) {
          case 'lg': return false
          case 'xl': return false
          default : return true
        }
      },
    },
  }
</script>

